<template>
  <div class="answer-box">
    <div v-if="!end">
        <div class="answer-title">{{ item.title }}</div>
        <div class="answer-kj">口诀：<span v-if="kjStatus">{{item.kj}}</span></div>
        <div class="answer-content" v-if="contentStatus" v-html="item.content"></div>
        <div class="btns">
        <mt-button type="danger" size="large" plain @click="kjStatus=!kjStatus">口诀</mt-button>
        <mt-button type="primary" size="large" plain @click="contentStatus=!contentStatus" >答案</mt-button>
        <mt-button type="primary" size="large" plain @click="next" >下一题</mt-button>
        </div>
    </div>
    <div v-else>
         全部回答完成！！！
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      item: {},
      kjStatus: false,
      contentStatus: false,
      end: false
    };
  },
  computed: {
    ...mapState({
      list: (state) => {
        return state.common.list;
      },
      answered: (state) => state.common.answered,
    }),
  },
  methods: {
    next() {
      this.kjStatus = false
      this.contentStatus = false
      if (this.list.length !== this.answered.length) {
        const index = this.getIndex();
        this.item = this.list[index];
        const a = [...this.answered]
        a.push(index)
        this.$store.commit('setAnswered', a)
      } else {
        // 答完
        this.end = true
      }
    },
    getIndex() {
      const len = this.list.length;
      let r = true;
      let i = 0;
      while (r) {
        i = this.rand(len);
        r = this.answered.includes(i);
      }
      return i;
    },
    // 0-n-1的随机数
    rand(n) {
      return Math.floor(Math.random() * n);
    },
  },
  created() {
      if(this.list.length === this.answered.length){
          this.$store.commit('setAnswered', [])
      }
  },
  mounted() {
    this.next();
  },
};
</script>
<style lang="scss" scoped>
.answer-box {
  padding: 30px 20px;
  .answer-title {
    font-size: 18px;
    line-height: 26px;
    font-weight: bold;
  }
  .answer-kj{
      line-height: 40px;
      font-size: 18px;
      text-indent: 2em;
      span{
          color: red;
      }
  }
  .answer-content{
     line-height: 30px;
     font-size: 16px;
     padding-left: 20px;
     box-sizing: border-box;
  }
  .btns{
      padding-top: 30px;
       /deep/ .mint-button{
          margin-bottom:20px
      }
  }
}
</style>